* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Inter, Times New Roman, Times, serif;
}

html, body {
  width: 100%;
  height: 100%;
}

.messageContainer {
  background-color: #ff69b4;
}

.formContainer {
  color: #fff;
  background-color: #333;
  border-radius: 5px;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  display: flex;
}

.formContainer form {
  align-items: center;
  gap: 10px;
  display: flex;
}

.formContainer label {
  color: #fff;
  font-size: 16px;
}

.formContainer input, .formContainer textarea {
  border: 1px solid #ccc;
  border-radius: 3px;
  padding: 5px;
}

.formContainer input {
  width: 150px;
}

.formContainer textarea {
  resize: none;
  width: 200px;
  height: 30px;
  font-size: 13px;
}

#submitButton {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 3px;
  padding: 5px 10px;
  font-weight: bold;
}

#submitButton:hover {
  background-color: #0056b3;
  scale: 110%;
}
